import React from 'react';
import '../assets/styles/Carousel.scss';

const Carousel = () => (
    <div className="main" id='carousel' >

        <div id="carousel" className="carousel slide carousel-fade" data-ride="carousel" data-pause='false' >
            <div className="carousel-inner">
                <div className="carousel-item active" data-interval="3000">
                    <img src="https://i.imgur.com/tquyJ0A.jpg" className="d-block w-100" alt="..." />
                </div>
                <div className="carousel-item" data-interval="3000">
                    <img src="https://i.imgur.com/H7tZC4L.jpg" className="d-block w-100" alt="..." />
                </div>
                <div className="carousel-item" data-interval="3000">
                    <img src="https://i.imgur.com/fgnCbg9.jpg" className="d-block w-100" alt="..." />
                </div>
                <div className='overlay' >
                    <div className="container">
                        <div className="row align-items-center">
                            <div className="col-md-8 offset-md-4 text-center text-md-right">
                                <h1>Visita Córdoba</h1>
                                <p className="d-none d-md-block" >Córdoba y sus paisajes invitan a sumergirse en una sucesión única de imágenes, lenguajes y sencaciones, que atraen a los turistas del país y del mundo, a descubrir las bellezas de esta provincia y tomar contacto con su impronta cultural e histórica, su perfil tradicional y moderno. Córdoba ocupa el sitio que le reservó el destino. Vení. Viví Córdoba.</p>
                                <a href="#" className='btn btn-outline-success'>Lugares</a>
                                <button type='button' className='btn btn-success ml-2' >Ver Hotel</button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            {/* <a class="carousel-control-prev" href="#carouselExampleFade" role="button" data-slide="prev">
                <span class="carousel-control-prev-icon" aria-hidden="true"></span>
                <span class="sr-only">Previous</span>
            </a>
            <a class="carousel-control-next" href="#carouselExampleFade" role="button" data-slide="next">
                <span class="carousel-control-next-icon" aria-hidden="true"></span>
                <span class="sr-only">Next</span>
            </a> */}
        </div>

    </div>
);

export default Carousel;