import React from 'react';
import '../assets/styles/Fluid.scss';

const Fluid = () => (
    <section className="place-time">
        <div className="container-fluid">
            <div className="row">
                <div className="col-12 col-md-6 pl-0 pr-0">
                    <img src="https://i.imgur.com/dQBxCRk.jpg" alt="..."/>
                </div>
                <div className="col-12 col-md-6 mb-4">
                    <h2 className='mt-4 mb-2' >Verano en las sierras</h2>
                    <p>Se caracterizan por formaciones montañosas (sierras) de mediana altura; el pico más alto es el Cerro Champaquí (2884 m), al este de Villa Dolores, y constituyen el extremo sur de la región de las Sierras Pampeanas. En esta zona hay cuatro cadenas montañosas principales; a saber de este a oeste: la Sierras Chicas, una cadena de laderas suaves que excepto en la vertiente occidental del famoso Cerro Uritorco se prolonga al norte por las Sierras de Ambargasta; al oeste, y casi paralelas a la Sierra Chica y las Sierras de Ambargasta se encuentran las mucho más agrestes Sierras Grandes y las Sierras de Pocho. Las Sierras Grandes continúan hacia el Sur en el cordón llamado Sierra de Comechingones.</p>
                    <a href="#" className='btn btn-outline-light'>Conocer más</a>
                </div>
            </div>
        </div>
    </section>
);

export default Fluid;