import React from 'react';
import emailjs from 'emailjs-com';
import '../assets/styles/ContactUs.scss';

export default function ContactUs() {

  function sendEmail(e) {
    e.preventDefault();

    emailjs.sendForm('gmail', 'template_turismo_ale', e.target, 'user_sFGeMInRraO3kCiz9ib04')
      .then((result) => {
          console.log(result.text);
      }, (error) => {
          console.log(error.text);
      });
      e.target.reset()
  }

  return (
    <div className="container mt-4">
      <div className="row">
        <div className="col text-center mb-2">
            <h2>Consultas!</h2>
        </div>
      </div>
      <form className="contact-form" onSubmit={sendEmail}>
        <input  className='hidden' type="hidden"   
                name="contact_number" />
        <input  className='text' type="text" 
                name="user_name" placeholder="Nombre" />
        <input  className='email' type="email" 
                name="user_email" placeholder="Email" />
        <textarea className='message' 
                name="message" placeholder="Envíame un mensaje..." />
        <input className='send' type="submit" value="Enviar" />
      </form>
    </div>
  );
}